.page-login {
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .page-login > div {
    background: skyblue;
    width: 300px;
    padding: 20px;
    border-radius: 10px;
    border: 1px solid gainsboro; }
    .page-login > div input {
      width: 100%;
      margin-bottom: 20px;
      border-radius: 100px;
      padding: 10px;
      border: 0; }
    .page-login > div button {
      padding: 10px;
      border-radius: 4px;
      width: 100%;
      border: 0;
      background: rgba(255, 255, 255, 0.5);
      cursor: pointer;
      transition: all 0.3s; }
      .page-login > div button:focus {
        outline: none; }
      .page-login > div button:hover {
        background: black;
        color: white; }
